.ContactData {
    margin: 20px auto;
    width: 80%;
    text-align: center;
    box-shadow: 0 2px 3px #ccc;
    border: 1px solid #eee;
    padding: 10px;
    box-sizing: border-box;
}

.ContactData .Input {
    display: block;
    width: 80%;
    padding: 8px;
    margin: 10px auto;
    border-radius: 4px;
}

@media (min-width: 600px) {
    .ContactData {
        width: 500px;
    }
}